@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    local("Rajdhani"),
    url("../fonts/Rajdhanimedium.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/Rajdhanimediumo.woff") format("woff"), /* Modern Browsers */
}

@font-face {
  font-family: "Rajdhani bold";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src:
    local("Rajdhani bold"),
    url("../fonts/Rajdhanibold.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/Rajdhanibold.woff") format("woff"), /* Modern Browsers */
}
