@font-face {
  font-family: "Rajdhani";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Rajdhani"), url("../fonts/Rajdhanimedium.woff2") format("woff2"), url("../fonts/Rajdhanimediumo.woff") format("woff"); }

@font-face {
  font-family: "Rajdhani bold";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Rajdhani bold"), url("../fonts/Rajdhanibold.woff2") format("woff2"), url("../fonts/Rajdhanibold.woff") format("woff"); }

.container {
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 10px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px; } }

@media (max-width: 719px) {
  .container {
    max-width: 719px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

a {
  text-decoration: none; }

*,
*::before,
*::after {
  box-sizing: border-box; }

img {
  width: 100%;
  height: auto; }

.hidden {
  display: none; }

body {
  font-family: "Rajdhani", "Arial", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

.include {
  display: none; }

.main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1360px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .main {
    display: flex;
    flex-direction: column;
    align-items: center; } }

