.slider {
  max-width: 600px; }

.slider__inner {
  padding: 0;
  position: relative; }

.slider__logo {
  position: absolute;
  top: 32px;
  left: 52px; }

.logo__image {
  width: 95px;
  height: 53px;
  background-color: transparent; }

.slider__image {
  max-width: 600px;
  max-height: 768px; }

.slider__information-wrapper {
  position: absolute;
  bottom: 52px;
  left: 32px;
  display: flex;
  flex-direction: column; }

.slider__buttons-wrapper,
.slider__pages {
  display: flex; }

.slider__buttons-wrapper {
  justify-content: space-between;
  max-width: 200px;
  align-items: center; }

.slider__information-header {
  margin-bottom: 32px; }

.slider__title,
.slider__text {
  margin-top: 0;
  color: #ffffff; }

.slider__title {
  margin-bottom: 5px;
  font-size: 72px;
