.slider__text {
  font-size: 28px;
  line-height: 32px;
  margin-bottom: 0; }

.slider__buttons {
  max-height: 32px; }

.slider__button {
  max-height: 32px;
  max-width: 32px;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
