.slider__button:active,
.slider__button--active {
  background-color: #ffffff;
  color: #8a94a6; }

.pages__text {
  margin: 0;
  color: #ffffff;
  font-size: 20px;
  line-height: 32px; }

.pages__text:not(:last-child) {
  padding-right: 5px; }

@media (max-width: 1023px) {
  .slider {
    max-width: 1023px;
    width: 100%;
    margin-bottom: 6px; }
  .slider__image {
    max-width: 100%;
    width: 100%;
    max-height: 600px; }
  .logo__image {
    width: 65px;
    height: 36px; }
  .slider__information-header {
    margin-bottom: 24px; }
  .slider__title {
    font-size: 64px;
    line-height: 64px; }
  .slider__text {
    font-size: 24px; } }

@media (max-width: 719px) {
  .slider {
    max-width: 767px; }
  .slider__image {
    max-height: 600px;
    height: 600px;
    object-fit: cover; }
  .slider__title {
    font-size: 48px;
    line-height: 48px; }
  .slider__text {
    font-size: 16px;
    line-height: 24px; } }

.catalog {
  max-width: 760px; }

.catalog__inner {
  padding-left: 2%;
  padding-right: 4%;
  display: flex;
  flex-direction: column;
  margin-top: 56px;
  margin-bottom: 0; }

.catalog__information {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px;
  max-width: 406px; }

.information__remark,
.information__title,
.information__text {
  margin-top: 0;
  color: #8a94a6; }

.information__remark,
.information__text {
  color: #8a94a6; }

.information__title,
.information__text {
  font-weight: 500; }

.information__remark {
  font-weight: 700;
  margin-bottom: 24px; }

.information__title {
  margin-bottom: 16px;
  font-size: 48px;
  line-height: 56px;
  color: #192b3f; }

.information__text {
  margin-bottom: 0;
  font-size: 20px;
  line-height: 32px; }

.catalog__checklist {
  display: flex;
  justify-content: space-between;
  max-width: 460px;
  margin-bottom: 40px;
  align-items: center;
  flex-wrap: wrap; }

.catalog__colors {
  display: flex;
  flex-direction: column; }

.color__header,
.size__header,
.counter__title,
.price__title {
  margin-top: 0;
  color: #8a94a6;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px; }

.color__header {
  margin-bottom: 8px; }

.color__list {
  list-style: none;
  margin: 0;
  padding-left: 0;
  display: flex;
  justify-content: space-between; }

.color__button {
  width: 28px;
  height: 28px;
  cursor: pointer;
  border-radius: 17px;
  border-width: 2px;
  border: 2px solid #ffffff; }

.color__button:not(:last-child) {
  margin-right: 12px; }

.color__button-black {
  background-color: #000000; }

.color__button-grey {
  background-color: #8a94a6; }

.color__button-blue {
  background-color: #3d5876; }

.color__button:hover,
.color__button:focus {
  outline: solid 2px #8a94a6; }

.color__button--active,
.color__button:active {
  outline: solid 2px #000000; }

.catalog__sizes {
  display: flex;
  flex-direction: column; }

.size__header {
  margin-bottom: 5px; }

.size__buttons-wrapper {
  display: flex;
  justify-content: space-between; }

.size__button {
  width: 32px;
  height: 32px;
  border: none;
  background-color: transparent;
  padding: 0;
  color: #8a94a6;
  text-transform: uppercase;
  cursor: pointer; }

.size__button:not(:last-child) {
  margin-right: 10px; }

.size__button:hover,
.size__button:focus {
  color: #8a94a6;
  border: 1px solid #8a94a6;
  border-radius: 17px; }

.size__button--active,
.size__button:active {
  color: #192b3f;
  border: 1px solid #192b3f;
  border-radius: 17px; }

.size__buttons:disabled {
  color: #e1e6ed; }

.catalog__guide {
  margin-top: 10px; }

.catalog__guide-link {
  color: #c9b8a8;
  border: 1px solid #c9b8a8;
  border-radius: 17px;
  padding: 3px 13px; }

.catalog__guide-link:hover,
.catalog__guide-link:focus {
  opacity: 0.5; }

.catalog__cost {
  display: flex;
  max-width: 460px;
  margin-bottom: 40px; }

.catalog__counter {
  padding-right: 83px; }

.counter__title {
  margin-bottom: 12px; }

.price__title {
  margin-bottom: 8px; }

.price__text {
  margin: 0;
  font-size: 40px;
  line-height: 40px;
  color: #c9b8a8; }

.price__zeros {
  font-size: 28px;
  line-height: 40px;
  color: #8a94a6; }

.counter__buttons {
  display: flex;
  align-items: center; }

.counter__input {
  border: none;
  font-size: 24px;
  line-height: 24px;
  width: 31px;
  height: 31px;
  text-align: center;
  background-color: #ffffff; }

.counter__input::-webkit-outer-spin-button,
.counter__input::-webkit-inner-spin-button {
  appearance: none; }

.counter__button {
  border: none;
  cursor: pointer;
  background-color: transparent;
  width: 26px;
  height: 26px;
  padding: 2px 0 0 0; }

.counter__button--active,
.counter__button:active {
  border: 1px solid #000000;
  border-radius: 17px; }

.catalog__details {
  margin-bottom: 32px; }

.details__information {
  margin-bottom: 72px;
  position: relative; }

.details__information::after {
  content: "";
  border-bottom: 1px solid #e1e6ed;
  width: 100%;
  top: -21px;
  position: absolute; }

.details__information::before {
  content: "";
  border-bottom: 1px solid #e1e6ed;
  width: 100%;
  top: -229px;
  position: absolute; }

.details__title,
.details__text,
.details__remark {
  margin-top: 0; }

.details__title,
.details__remark {
  color: #8a94a6; }

.details__title,
.details__text {
  margin-bottom: 8px; }

.details__title {
  text-transform: uppercase;
  font-weight: 700; }

.details__text {
  color: #192b3f; }

.details__remark {
  margin-bottom: 0;
  line-height: 20px; }

.details__button {
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #ffffff;
  background-color: #192b3f;
  border: none;
  cursor: pointer;
  font-size: 24px;
  line-height: 32px; }

.details__button:hover,
.details__button:focus {
  background-color: #2e4055; }

.details__button:active {
  background-color: #3b536d; }

.catalog__list {
  list-style: none;
  margin-top: 0;
  margin-bottom: 24px;
  padding-left: 0;
  display: flex;
  flex-direction: column; }

.list__item {
  position: relative;
  cursor: pointer; }

.list__item::after {
  content: "";
  border-bottom: 1px solid #e1e6ed;
  width: 100%;
  bottom: -9px;
  position: absolute; }

.list__title {
  margin: 0;
  font-size: 24px;
  line-height: 32px; }

.list__item:not(:last-child) {
  margin-bottom: 24px; }

.list__text {
  margin: 0;
  height: 0;
  overflow: hidden; }

.list__item.list__item--active .list__text {
  height: 60px;
  overflow: visible; }

.list__button {
  position: absolute;
  background-color: transparent;
  border: none;
  padding: 0;
  right: 0;
  top: 0;
  cursor: pointer;
  background-image: url("../img/plus-icon.svg");
  background-repeat: no-repeat;
  width: 25px;
  height: 25px; }

.list__item.list__item--active .list__button {
  background-image: url("../img/minus-icon.svg"); }

@media (max-width: 1023px) {
  .catalog {
    max-width: 100%; }
  .catalog__inner {
    margin-top: 0;
    padding-left: 4%; }
  .information__remark {
    margin-bottom: 16px; }
  .catalog__information {
    margin-bottom: 28px;
    max-width: 100%; }
  .catalog__checklist {
    margin-bottom: 36px; }
  .catalog__cost {
    margin-bottom: 32px; }
  .details__information,
  .catalog__details {
    margin-bottom: 40px; }
  .details__information::before {
    top: -223px; } }

@media (max-width: 719px) {
  .information__title {
    font-size: 32px;
    line-height: 32px; }
  .information__text {
    font-size: 18px;
    line-height: 28px; }
  .catalog__information,
  .catalog__checklist {
    margin-bottom: 32px; }
  .catalog__checklist {
    flex-direction: column;
    align-items: flex-start; }
  .color__group,
  .catalog__sizes {
    margin-bottom: 24px; }
  .size__buttons-wrapper {
    transform: translateX(-6px); }
  .catalog__cost {
    margin-bottom: 44px; }
  .catalog__list {
    margin-bottom: 48px; }
  .details__information::before {
    top: -370px; }
  .list__item.list__item--active .list__text {
    height: 125px;
    overflow: visible; } }

.catalog__collection {
  display: flex;
  flex-direction: column;
  margin-bottom: 64px; }

.collection__header {
  margin-top: 0;
  margin-bottom: 8px;
  color: #8a94a6;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 24px; }

.collection__pictures {
