  flex-direction: column;
  position: absolute;
  bottom: 12px;
  left: 16px; }

.collection__title,
.collection__text {
  margin-top: 0;
  color: #ffffff; }

.collection__title {
  margin-bottom: 5px;
  font-size: 24px;
  line-height: 24px; }

.collection__text {
  margin-bottom: 0;
  font-size: 14px;
